<template>
	<div class="wrapper">
		<div class="searchBox">
			<router-link to="/novalSearch">
				<i class="icon iconfont icon-sousuo"></i>
			</router-link>
		</div>
		<dl  class="info-item info-small no-border" v-for="(item,index) in list" :key="index" :id="item.id">
			<dt >
				<img  :src="item.book_cover" alt="">
			</dt>
			<dd  class="info-title">{{item.bookname}}</dd>
			<dd class="info-author"><span><i class="icon iconfont icon-31wode"></i>{{item.author_name}}</span></dd>
			<dd class="info-author"><span><i class="icon iconfont icon-dianzan"></i>{{item.search_heat}}</span><span class="right mr10"><i class="icon iconfont icon-chanpincanshu"></i>{{item.chapterNum}}</span></dd>
			<dd class="info-time">{{item.class_name}}</dd>
			 <dd  class="info-note">
				{{ item.book_info }}
			</dd> 
		    <dd class="cover">
		       <router-link  :to="{name:'novalDetails',params:{id:index,novalName:item.bookname}}"></router-link> 
		    </dd>
		</dl>
	</div>
</template>
<script>
	
	export default {
		data(){
			return{
				list:[]
			}
		},
		created(){
			this.$axios.get("https://www.apiopen.top/novelApi")
			.then(res=>{
				this.list = res.data.data
			})
			.catch(err=>{
				console.log(err);
			})
		}
	}
</script>
<style scoped lang="less">
	.wrapper{
		padding:10px 12px ;
		.searchBox{
			width: 100%;
			height: 30px;
			text-align: center;
			line-height: 28px;
			border: 1px solid #ccc;
			background: #f3f3f3;
			border-radius: 5px;
			font-size: 20px;
			font-weight: 700;
			a{
				display: block;
				width: 100%;
				height: 100%;
			}
		}
	}
	  .info-list {
    padding: 0 12px;
  }

  .info-small {
    position: relative;
    height: 180px;
    padding: 12.5px 0 12.5px 125px;
  }

  .info-small dt {
    position: absolute;
    top: 12.5px;
    left: 0;
    width: 115px;
    height: 155px;
    overflow: hidden;
  }

  .info-item dt img {
    width: 100%;
    height: 100%;
  }

  .info-title {
    font-size: 15px;
    line-height: 24px;
    color: #000;
  }

  .info-small .info-title {
    height: 20px;
    line-height: 20px;
    font-size: 18px;
    font-weight: 700;
    overflow: hidden;
  }

   .info-small .info-author{ 
    font-size: 14px;
    color: #ccc;
    height: 28px;
    overflow: hidden;
	line-height: 28px;
    }
    .info-small .info-author i{
		margin-right: 3px; 
    }
   .info-small .info-category{ 
    font-size: 12px;
    color: #ccc;
    height: 20px;
    overflow: hidden;
    margin: 1px 0;
    }
      .info-small  .info-tags{
      	   font-size: 12px;
    color: #ccc;
    height: 20px;
    overflow: hidden;
    margin: 1px 0;
      }
  .info-note {
    margin-top: 5px;
    height: 20px;
    line-height: 20px;
    font-size: 10px;
    color: #999;
    overflow: hidden;
  }

  .left {
    float: left;
  }

  .right {
    float: right;
  }

  .cover {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .cover a{
     position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  a {
    color: #333;
    text-decoration: none;
  }

  .info-three {
    padding: 8px 0;
  }

  .info-item {
    position: relative;
    border-bottom: 1px dotted #ddd;
  }

  .info-pic-list {
    margin-top: 5px;
    overflow: hidden;
  }

  .info-pic-list img {
    float: left;
    width: 32%;
  }

  .info-pic-list img + img {
    margin-left: 2%;
  }

  .info-note {
    margin-top: 5px;
    height: 55px;
    line-height: 19px;
    font-size: 10px;
    color: #999;
    overflow: hidden;
  }

.similar h2{ margin:  5px 0; }
  .nav {
    position: relative;
    width: 100%;
    height: 205px;
    overflow: hidden;
  }

  .nav-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }

  .nav-list {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height:220px;
    overflow-x: scroll;
    overflow-y: hidden; 
  }

  .nav ul {
    position: relative;
    white-space: nowrap;
    height: 205px;
    z-index: 0;

  }

  .nav ul li {
    display: inline-block;
    height: 205px;
 width: 115px;
    text-align: center;
    margin: 0 5px;vertical-align:top;
  }
    .nav ul li .pic{
    display: inline-block;
    height: 150px;
    width: 115px;
    text-align: center;
  }
  .nav ul li .pic img { width: 100%; height: 100%; }
.nav ul li .desc{ width: 100%; white-space:normal; font-size: 12px; color: #ccc; height: 35px; line-height: 17px;}
.nav ul li .author { width: 100%;  white-space:normal; color: #000; font-size:  14px;}

  .nav ul li.act a {
    color: #733821;
  }

  .nav ul li a {
    display: inline-block;
    position: relative;
    font-size: 15px;
    color: #000;
  }
  .mr10{
  	margin-right: 10px; 
  }
</style>


